#sources {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: stretch;
  align-content: flex-start;

  margin: 0 auto;

  .chart-container {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;

    padding: 0 1rem;

    .chart {
      flex: 1;
    }
  }
}

.chart-label {
  font-family: $sans-bold;
  font-size: 1.8rem;
  text-align: center;
  opacity: 1;
  transition: opacity 250ms;
  color: $primary-font;
  &.opaque {
    opacity: 0;
  }
}

.legend {
  &.hidden {
    display: none;
  }
}

.chart-tooltip {
  position: absolute;
  display: none;
  transform: translate(-50%, -100%);

  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;

  padding: 2rem;
  border: 1px solid $borders;
  background-color: rgba(255, 255, 255, 0.95);

  &.hidden {
    display: none;
  }

  .department,
  .title {
    color: $secondary-font;
    font-family: $sans-bold;
    font-size: 1.4rem;
    text-align: center;

    padding-bottom: 0.75rem;
    margin-bottom: 0.25rem;
    border-bottom: 1px solid $borders;
  }

  .project {
    color: $primary-font;
    font-family: $sans-bold;
    font-size: 1.6rem;
    text-align: center;
  }

  .director {
    color: $primary-font;
    font-family: $sans-bold;
    font-size: 1.4rem;
    text-align: center;
    font-style: italic;
  }

  .total {
    font-family: $sans-bold;
    font-size: 1.6rem;
    text-align: center;
  }

  .percent {
    color: $primary-font;
    font-family: $sans-bold;
    font-size: 1.4rem;
    text-align: center;
  }

  &[data-topic="AG"] .total,
  &[data-topic="AG"] .director {
    color: $agsciences;
  }

  &[data-topic="ENV"] .total,
  &[data-topic="ENV"] .director {
    color: $envsciences;
  }

  &[data-topic="HUMAN"] .total,
  &[data-topic="HUMAN"] .director {
    color: $humansocialsciences;
  }

  &:before {
    content: "";
    display: block;
    position: absolute;
    left: 50%;
    bottom: -10px;
    width: 0;
    height: 0;
    border-left: 10px solid transparent;
    border-right: 10px solid transparent;
    border-top: 10px solid $borders;
    transform: translate(-50%, 0);
  }

  &:after {
    content: "";
    display: block;
    position: absolute;
    left: 50%;
    bottom: -9px;
    width: 0;
    height: 0;
    border-left: 10px solid transparent;
    border-right: 10px solid transparent;
    border-top: 10px solid rgba(255, 255, 255, 0.95);
    transform: translate(-50%, 0);
  }
}
