// fonts
@font-face {
    font-family: 'proxima-nova-r';
    src: url('../media/fonts/proximanova-regular-webfont.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}
@font-face {
    font-family: 'proxima-nova-i';
    src: url('../media/fonts/proximanova-regularit-webfont.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}
@font-face {
    font-family: 'proxima-nova-b';
    src: url('../media/fonts/proximanova-bold-webfont.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}
@font-face {
    font-family: 'ryman';
    src: url('../media/fonts/ryman-eco-webfont.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}

$sans-italic:   "proxima-nova-i",sans-serif;
$sans-bold:     "proxima-nova-b",sans-serif;
$sans-reg:      "proxima-nova-r",sans-serif;
$ryman:         "ryman",serif;

$primary-color:     #002855;
$secondary-color:   #DAAA00;
$tertiary-color:    transparentize($secondary-color, .80);

$gold:              #DAAA00;
$blue:              #002855;

$accent:            #CCD4E0;

$a-link:            transparentize($primary-color, .2);

$primary-font: rgba(31,31,31,.99); //
$secondary-font: rgba(31,31,31,.80); //
$tertiary-font: rgba(31,31,31,.40); //

$dark: #252525;

$bg-color: transparentize(#002855, .9); // background
$borders: #C8C7CC; // border color

$nav-active: #fff;

$section-padding: 6rem 0rem;

// UC Davis Colors
// Supplemental bright color palette for UC Davis
$agsciences: #43B02A;
$envsciences: #0078FF;
$humansocialsciences: #FFC72C;

$bodega: #002A3A;

$box-shadow: 0 0 0 1px rgba(49,49,93,.03), 0 2px 5px 0 rgba(49,49,93,.1), 0 1px 2px 0 rgba(0,0,0,.08);
// mixins

@mixin breakpoint($point) {
    @if $point==sm {
        @media (min-width: 576px) {
            @content ;
        }
    }
    @if $point==md {
        @media (min-width: 768px) {
            @content ;
        }
    }
    @if $point==lg {
        @media (min-width: 992px) {
            @content ;
        }
    }
    @if $point==xl {
        @media (min-width: 1200px) {
            @content ;
        }
    }
    @if $point==smmax {
        @media (max-width: 576px) {
            @content ;
        }
    }
    @if $point==mdmax {
        @media (max-width: 768px) {
            @content ;
        }
    }
    @if $point==lgmax {
        @media (max-width: 992px) {
            @content ;
        }
    }
    @if $point==xlmax {
        @media (max-width: 1200px) {
            @content ;
        }
    }
}
.container {
  padding: 0;
  width: 95%;
  max-width:1440px;
  @include breakpoint (sm) {
    width: 93%;
    max-width:1440px;
  }
  @include breakpoint (md) {
    width: 91%;
    max-width:1440px;
  }
  @include breakpoint(lg) {
    width: 90%;
    max-width:1440px;
  }
  @include breakpoint(xl) {
    width: 85%;
    max-width:1440px;
  }
}

@mixin fader {
  -o-transition:.25s;
  -ms-transition:.25s;
  -moz-transition:.25s;
  -webkit-transition:.25s;
  /* ...and now for the proper property */
  transition:.25s;
}



